/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/

/* Buttons */

button,
button:active,
button:focus,
button:hover,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: $color__border-button;
	border-color: $color__border;
	border-radius: 3px;
	background: $color__background-button;
	color: $color__text-button;
	font-size: 1.25rem;
	font-weight: 700;
	line-height: 1;
	padding: 0.75rem 2.25rem;

	&:hover {
		border-color: #0c0 rgb(28, 153, 68) rgb(9, 116, 39);
		box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.8), inset 0 15px 17px rgba(184, 233, 170, 0.8), inset 0 -5px 12px rgba(16, 213, 58, 0.02);
		text-shadow: 0x 0px 90px rgb(163, 250, 196);
	}

	&:active,
	&:focus {
		border-color: #0c0 rgb(28, 153, 68) rgb(9, 116, 39);
		box-shadow: inset 0 -1px 0 rgba(255, 255, 255, 0.5), inset 0 15px 17px rgba(184, 233, 170, 0.8);
	}
}

/* Fields */

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
	color: #fff;
	background: transparent;
	font-weight: 500;
	border: 2px solid $color__main-brand;
	border-radius: 3px;

	&:hover {
		border-color: #0c0 rgb(28, 153, 68) rgb(9, 116, 39);
		box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.8), inset 0 15px 17px rgba(184, 233, 170, 0.8), inset 0 -5px 12px rgba(16, 213, 58, 0.02);
		text-shadow: 0x 0px 90px rgb(163, 250, 196);
	}

	&:active,
	&:focus {
		border-color: #0c0 rgb(28, 153, 68) rgb(9, 116, 39);
		box-shadow: inset 0 -1px 0 rgba(255, 255, 255, 0.5), inset 0 15px 17px rgba(184, 233, 170, 0.8);
	}
}

.search-submit {
	border: none;
}

.search-form {
	display: flex;
	flex-flow: row wrap;
	justify-content: flex-start;
	align-items: center;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
	padding: 3px;
}

textarea {
	padding: 0.5rem 0.5rem;
	width: 100%;
}