/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/

/* Text meant only for screen readers. */
.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;

	&:focus {
		background-color: $color__background-screen;
		border-radius: 3px;
		box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
		clip: auto !important;
		color: $color__text-screen;
		display: block;
		font-size: .875rem;
		font-weight: bold;
		height: auto;
		left: 5px;
		line-height: normal;
		padding: 15px 23px 14px;
		text-decoration: none;
		top: 5px;
		width: auto;
		z-index: 100000;
		/* Above WP toolbar. */
	}
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/

.alignleft {
	display: inline;
	margin-right: $base__space-unit;
}

.alignright {
	display: inline;

	margin-left: $base__space-unit;
}

.aligncenter {
	clear: both;
	@include center-block;
}

.container {
	padding: 2.5%;
	width: 100%;

	display: flex;
	flex-flow: column wrap;
	justify-content: space-around;
}

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/

.clearfix {
	clear: both;
	content: "";
	display: table;
}